<template>
	<div class="shopping_car">
    <ul class="goods" v-if="goodsList.length !== 0">
      <li v-for="(item, key) in goodsList">
        <section class="pointer" @click="selectOne(key)">
          <img src="../../assets/shopImage/select.png" alt=""  v-if="item.selected">
          <img src="../../assets/shopImage/select_a.png" alt="" v-else>
        </section>
        <div class="good_info">
          <img :src="item.pic_url" alt="" class="good_info_img">
          <section class="good_attr">
            <section class="good_attr_t">
              <span class="info_desc">{{item.good_name}}</span>
              <span class="info_price">¥ {{item.good_price}}</span>
            </section>
            <section class="good_attr_d">
              <div>
                <span @click="cut(item)">
                  <img src="../../assets/shopImage/cut.png" alt=""></span><span class="info_num">{{item.good_num}}</span><span @click="add(item)">
                <img src="../../assets/shopImage/add.png" alt="">
              </span>
              </div>
              <img src="../../assets/shopImage/delete.png" alt="" class="good_delete" @click="deleteGood(item, key)">
            </section>
          </section>
        </div>
      </li>
    </ul>
    <div v-if="goodsList.length === 0" class="no_goods">
      <img src="../../assets/no_shop.png" alt="" ><br>
    </div>
    <footer class="car_foot">
      <section class="car_foot_l" @click="allSeleted">
        <div class="pointer">
          <img src="../../assets/shopImage/select.png" alt="" v-if="selected">
          <img src="../../assets/shopImage/select_a.png" alt="" v-else>
        </div>
        <span class="info_price">全选</span>
      </section>
      <section class="car_foot_c">
        <span style="margin-right: 0.2rem">总价:</span>
        <span>¥ {{allPrice}}</span>
      </section>
      <section class="car_foot_r">
        <a href="javascript:void(0)" @click="pay">结算(0)</a>
      </section>
    </footer>
	</div>
</template>
<script type="text/javascript">
  import { Toast} from 'mint-ui';
  import { Indicator} from 'mint-ui';
	export default{
		data(){
			return {
          goodsList: [],
          selected: true,
          allPrice: 0
			}
		},
    methods: {
		  //结算
      pay(){
        let orderList = this.goodsList.filter(item => {
          return item.selected === false;
        }).map(item => {
          return item.id;
        });
        if (orderList.length === 0) {
          Toast({
            message: '请选择要购买的商品',
            duration: 1000
          });
        } else {
          Indicator.open();
          this.post('home/Shop/showAllShop', {
            idList: orderList.toString()
          }).then(res => {
            Indicator.close();
            if (res.data.code === 10000){
              this.$router.push({ name: 'setOrder', params: { id: orderList.toString() }});
            } else{
              Toast({
                message: res.data.info,
                duration: 1000
              });
            }
          })
        }
      },
      deleteGood(item, index){
        this.post('home/Shop/delTempShop', {
          id: item.id
        }).then(res => {
          if (res.data.code === 10000) {
            this.goodsList.splice(index, 1);
          } else Toast({
            message: res.data.info,
            duration: 1000
          });
        })
      },
      //购物车增加
      addShop(id, num){
        Indicator.open();
        this.post('home/Shop/setCartNumber', {
          good_id: id,
          number: num
        }).then(res => {
          Indicator.close();
          if (res.data.code === 10000) {

          } else   Toast({
            message: res.data.info,
            duration: 1000
          });
        });
      },
      //加减
      add(item){
        item.good_num ++;
        //计算价格
        if (!item.selected) {
          this.allPrice +=  (item.good_price - 0);
        }
        this.addShop(item.good_id, item.good_num);
      },
      cut(item){
        if (item.good_num !== 0) {
          item.good_num --;
          this.addShop(item.good_id, item.good_num);
          if (!item.selected) {
            this.allPrice -= (item.good_price - 0);
          }

        }
      },
      selectOne(index){
        this.goodsList[index]['selected'] = !this.goodsList[index]['selected'];
        this.goodsList.splice(index, 1, this.goodsList[index]);
        let list = this.goodsList.filter(item => {
          return item['selected'] === true;
        });
        this.selected = list.length !== 0 ;
      //  计算总价格d_price;
        if (this.goodsList[index]['selected']) {
          this.allPrice -= this.goodsList[index].good_price * this.goodsList[index].good_num;
        } else {
          this.allPrice += this.goodsList[index].good_price * this.goodsList[index].good_num;
        }

      },
		  //全选
      allSeleted(){
        this.selected = !this.selected;
        if (!this.selected) {
          //全选
          this.goodsList.forEach(item => {
            item['selected'] = false;
            this.allPrice += item.good_price * item.good_num;
          });
        } else {
          this.goodsList.forEach(item => {
            item['selected'] = true;
          });
          this.allPrice = 0;
        }
      },
		  // 获取购物商品信息
      getGoodsInfo(){
        this.post('home/Shop/getAllTemp', {}).then(res => {
          if (res.data.code === 10000) {
            this.goodsList = res.data.info;
            this.goodsList.forEach(item => {
              item.pic_url = this.imageURL(item.pic_url);
              item['selected'] = true;
            })
          }
        })
      }
    },
    mounted (){
		  this.getGoodsInfo();
    }
	}
</script>
<style type="text/css" scoped>
.shopping_car {
  width: 100%;
  height: calc(100vh - 1rem);
  background-color: #F2F2F2;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.goods {
  flex: 1;
  overflow-y: auto;
  -webkit-overflow-scrolling : touch;
}
.goods li{
  background-color: #ffffff;
  padding: 0.2rem 0.3rem 0.2rem 0.2rem;
  border-bottom: 1px solid #F2F2F2;
  display: flex;
  align-items: center;
}
.pointer img{
  width: .4rem;
  height: .4rem;
}
.good_info {
  flex: 1;
  display: flex;
}
.good_info_img {
  margin:0 0.2rem;
  width: 1.6rem;
  height: 1.6rem;
}
.good_attr {
  padding: 0.1rem;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.good_attr_t {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.info_desc {
  font-size: 0.26rem;
  color: #111111;
}
.info_num {
  font-size: 0.24rem;
  color: #666666;
}
.good_attr_d {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
/*加减*/
.good_attr_d span {
  display: inline-block;
  width: 0.65rem;
  height: 0.5rem;
  line-height: 0.48rem;
  border: 1px solid #aeaeae;
  text-align: center;
  font-size: 0.28rem;
}
.good_attr_d span img {
  width: 0.2rem;
  height: 0.2rem;
}
.good_attr_d span:first-child {
  border-top-left-radius: 0.3rem;
  border-bottom-left-radius: 0.3rem;
  border-right: none;
}
.good_attr_d span:last-child {
  border-top-right-radius: 0.3rem;
  border-bottom-right-radius: 0.3rem;
  border-left: none;
}
.info_price {
  font-size: 0.3rem;
  color: #FF5773;
}
.good_delete {
  width: 0.3rem;
  height: 0.3rem;
}
  .car_foot {
    padding-left: 0.2rem ;
    flex-basis: 1rem;
    background-color: #ffffff;
    display: flex;
    align-items: center;
  }
  .car_foot_l {
    display: flex;
    align-items: center;
  }
.car_foot_l span:last-child {
  margin-left: 0.1rem;
}
.car_foot_c {
    text-align: center;
    flex: 1;
  }
  .car_foot_c>span{
    font-size: 0.3rem;
    color: #666666;
  }
  .car_foot_c>span:nth-child(2) {
    font-size: 0.34rem;
    color: #FF5773;
  }
  .car_foot_r a {
    text-align: center;
    line-height: 1rem;
    display: inline-block;
    width: 1.66rem;
    height: 1rem;
    font-size: 0.34rem;
    color: #ffffff;
    background-color: #FF5773;
  }
.no_goods{
  flex: 1;
  text-align: center;
  display: inline-block;
  /*background-color: #FFFFFF;*/
}
.no_goods img{
  margin-top: 25vh;
  margin-left: 1rem;
  width: 3rem;
  height: 3rem;
}
</style>



